* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  font-family: 'Poppins', sans-serif;
}

$cor-primaria: #f9db79;
$hover: #ef6bb6;

@mixin center() {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@mixin flex($justify) {
  display: flex;
  justify-content: $justify;
  align-items: center;
  flex-wrap: wrap;
}

@mixin banner($url) {
  width: 100%;
  height: 80vh;
  padding-top: 80px;
  background-image: url($url);
  background-position: center 70%;
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
}

@mixin titulo-1() {
  text-shadow: 1px 5px 3px #454545;
  color: #ffffff;
  font-size: 3.5rem;
  text-align: center;
}

@mixin titulo-2() {
  font-size: 2rem;
  margin-bottom: 50px;
}

@mixin espaco-textos() {
  text-align: center;
  margin-top: 100px;
  margin-bottom: 100px;
}

img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
}
